@import '../../styles/abstract/constants.scss';
@import '../../styles/abstract/mixins.scss';
@import '../../styles/abstract/placeholders.scss';

.button {
  @include button;
  @include text(
    $color: $white,
    $font-size: 1.8rem,
    $font-weight: $fw-medium,
    $line-height: 1.3
  );
  letter-spacing: 0.9px;
  padding: 0.5rem;
  padding-left: 3rem;
  gap: 2.5rem;
  border-radius: 1.5rem;
  background: linear-gradient(96deg, #dd3197 0%, #822faf 101.4%);
  @include button-hover;

  &_type {
    &_empty {
      background: $white;
      border: 1px solid #dd3197;
      color: $pink;

      .button__image {
        background: linear-gradient(96deg, #dd3197 0%, #822faf 101.4%);
      }
    }
  }

  @include media-small {
    @include text(
      $color: $white,
      $font-size: 1.5rem,
      $font-weight: $fw-medium,
      $line-height: 1.4
    );
    padding: 0.3rem;
    padding-left: 2rem;
    border-radius: 1rem;
    gap: 2rem;
  }
}

.button__image {
  padding: 1.3rem;
  width: 5rem;
  display: flex;
  aspect-ratio: 1 / 1;
  background-color: $white;
  border-radius: 1rem;

  @include media-small {
    padding: 0.8rem;
    width: 3.6rem;
  }
}
